@import './src/common/styles/sass/variables';
@import './src/common/styles/sass/mixins';

.title {
  @include fontFamily($fontSecondary, 700);
  @include fontSize(34px, 1.2);
  @media (min-width: 576px) {
    font-size: 40px;
  }
  @media (min-width: 1200px) {
    font-size: 60px;
  }
}


